.banner {
  /* background-image: url('https://wallpaperaccess.com/full/2703652.png'); */
  background-size: cover;
  height: 100vh;
  color: #fff;
  position: relative;
  display: flex;
  align-items: center;
}
.content{
  padding:140px;
  height: 190px;
}
.title{
  font-size: 2rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.button{
  color:#fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  margin-right: 1rem;
}
.button:hover{
  color: black;
  background-color: #e6e6e6;
}
.description{
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 1rem;
  height: 80px;
  max-width: 360px;
}
.overlay{
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(180deg,transparent,rgba(0,0,0,0.1),#000);
}